@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components{
  .tr{
    @apply text-sm text-gray-900 font-light px-6 py-4 whitespace-nowrap
  }

  .navbarTitle{
    @apply block py-2 pl-3 pr-4 text-gray-700 rounded hover:bg-gray-100 md:hover:bg-transparent md:hover:text-red-700 md:p-0 md:dark:hover:text-gray-900 dark:text-gray-600 dark:hover:bg-gray-900 dark:hover:text-gray-900 md:dark:hover:bg-transparent dark:border-gray-600
  }

  .navbartitle2{
    @apply block py-2 pl-3 pr-4 text-gray-700 border-b border-gray-100 hover:bg-gray-50 lg:hover:bg-transparent lg:border-0 lg:hover:text-red-600 lg:p-0
  }

  .whiteButton{
   @apply inline-flex items-center justify-center w-full px-5 py-3 text-sm font-medium text-center text-gray-900 border border-gray-200 rounded-lg sm:w-auto hover:bg-gray-100 focus:ring-4 focus:ring-gray-100
  }
  .login{
    @apply text-white bg-red-600 hover:bg-red-700 focus:ring-4 focus:ring-red-300 font-medium rounded-lg text-sm px-4 lg:px-5 py-2 lg:py-2.5 sm:mr-2 lg:mr-0 focus:outline-none
   }

   .input{
    @apply bg-gray-50 border border-gray-300 text-gray-900 text-sm rounded-lg focus:ring-blue-500 focus:border-blue-500 block w-full p-2.5 
   }
   .label{
    @apply block mb-2 text-sm font-medium text-gray-900 
   }
} 